// API
class ApiClient {
    baseUrl;
    constructor() {
        // Rails APIURLRESTful
        this.baseUrl = '';
    }
    // HTMLCSRF
    // RailsCSRF
    getCSRFToken() {
        const meta = document.querySelector('meta[name="csrf-token"]');
        return meta?.content || '';
    }
    // FormData
    // RailsFormData
    createFormData(data, prefix = '') {
        const formData = new FormData();
        Object.entries(data).forEach(([key, value]) => {
            if (value !== undefined && value !== null) {
                const fieldName = prefix ? `${prefix}[${key}]` : key;
                formData.append(fieldName, String(value));
            }
        });
        return formData;
    }
    // HTTP
    // API
    async request(endpoint, options = {}) {
        const url = `${this.baseUrl}${endpoint}`;
        // CSRF
        const csrfToken = this.getCSRFToken();
        const headers = {
            'X-CSRF-Token': csrfToken,
            'Accept': 'application/json',
            ...options.headers,
        };
        // FormDataContent-Type
        if (!(options.body instanceof FormData)) {
            headers['Content-Type'] = 'application/json';
        }
        // AbortController
        const controller = new AbortController();
        const timeoutId = setTimeout(() => {
            controller.abort();
        }, options.timeout ?? 30000); // 30
        try {
            const response = await fetch(url, {
                ...options,
                headers,
                signal: controller.signal,
            });
            clearTimeout(timeoutId);
            if (!response.ok) {
                // Content-Type
                const contentType = response.headers.get('content-type');
                if (contentType !== null && contentType.includes('application/json')) {
                    // JSON
                    const errorResponse = await response.json();
                    const apiError = new Error(errorResponse.error || 'API request failed');
                    apiError.status = response.status;
                    if (errorResponse.details) {
                        apiError.details = errorResponse.details;
                    }
                    throw apiError;
                }
                else {
                    // HTML406 Not Acceptable
                    const htmlResponse = await response.text();
                    console.error('Non-JSON response received:', htmlResponse);
                    const apiError = new Error(`Server error (${response.status}): Expected JSON but received HTML. Please check server configuration.`);
                    apiError.status = response.status;
                    throw apiError;
                }
            }
            // Content-Type
            const contentType = response.headers.get('content-type');
            if (contentType === null || !contentType.includes('application/json')) {
                const htmlResponse = await response.text();
                console.error('Non-JSON success response received:', htmlResponse);
                throw new Error('Expected JSON response but received HTML');
            }
            const apiResponse = await response.json();
            return apiResponse.data;
        }
        catch (error) {
            clearTimeout(timeoutId);
            // 
            if (error instanceof Error && error.name === 'AbortError') {
                const timeoutError = new Error('Request timeout');
                timeoutError.status = 408;
                console.error('API Timeout:', timeoutError);
                throw timeoutError;
            }
            console.error('API Error:', error);
            throw error;
        }
    }
    // API - 
    // GET
    async getBusinessCards(params) {
        const queryString = params ? new URLSearchParams(params).toString() : '';
        const endpoint = `/business_cards${queryString ? `?${queryString}` : ''}`;
        return await this.request(endpoint);
    }
    // API
    // ID
    async getBusinessCard(id) {
        return await this.request(`/business_cards/${id}`);
    }
    // API
    // POST
    async createBusinessCard(data) {
        const formData = this.createFormData(data, 'business_card');
        return await this.request('/business_cards', {
            method: 'POST',
            body: formData,
        });
    }
    // API - FormData
    // PATCH
    async updateBusinessCard(id, data) {
        const formData = this.createFormData(data, 'business_card');
        return await this.request(`/business_cards/${id}`, {
            method: 'PATCH',
            body: formData,
        });
    }
    // API
    // DELETE
    async deleteBusinessCard(id) {
        await this.request(`/business_cards/${id}`, {
            method: 'DELETE',
        });
    }
    // API
    // POST
    async createUser(data) {
        const formData = this.createFormData(data, 'user');
        return await this.request('/users', {
            method: 'POST',
            body: formData,
        });
    }
    // API
    // POST
    async loginUser(data) {
        // remember_meFormData
        const sessionData = {
            email: data.email,
            password: data.password,
            ...(data.remember_me !== undefined && {
                remember_me: data.remember_me ? '1' : '0'
            })
        };
        const formData = this.createFormData(sessionData, 'session');
        return await this.request('/login', {
            method: 'POST',
            body: formData,
        });
    }
    // API - FormData
    // PATCH
    async updateUserProfile(id, data) {
        const formData = this.createFormData(data, 'user');
        return await this.request(`/users/${id}`, {
            method: 'PATCH',
            body: formData,
        });
    }
    // API
    // GET
    async getCurrentUser() {
        return await this.request('/current_user');
    }
}
// 
// API
export const apiClient = new ApiClient();
export const deleteBusinessCard = (id) => {
    console.log("Deleting business card with ID:", id);
    return apiClient.deleteBusinessCard(id);
};
export const getBusinessCards = (params) => {
    return apiClient.getBusinessCards(params);
};
export const getBusinessCard = (id) => {
    return apiClient.getBusinessCard(id);
};
export const createBusinessCard = (data) => {
    return apiClient.createBusinessCard(data);
};
export const updateBusinessCard = (id, data) => {
    return apiClient.updateBusinessCard(id, data);
};
// 
export const createUser = (data) => {
    return apiClient.createUser(data);
};
export const loginUser = (data) => {
    return apiClient.loginUser(data);
};
export const updateUserProfile = (id, data) => {
    return apiClient.updateUserProfile(id, data);
};
export const getCurrentUser = () => {
    return apiClient.getCurrentUser();
};
