import { createSlice, createAsyncThunk } from '@reduxjs/toolkit';
import { getBusinessCards, deleteBusinessCard as deleteBusinessCardApi, createBusinessCard as createBusinessCardApi, updateBusinessCard as updateBusinessCardApi } from '@/utils/api';
// 
const initialState = {
    cards: [],
    filteredCards: [],
    searchQuery: '',
    loading: false,
    error: null,
    successMessage: null,
};
// : 
export const fetchBusinessCards = createAsyncThunk('businessCards/fetchBusinessCards', async () => {
    return await getBusinessCards();
});
// : 
export const createBusinessCardThunk = createAsyncThunk('businessCards/createBusinessCard', async (cardData) => {
    return await createBusinessCardApi(cardData);
});
// : 
export const updateBusinessCardThunk = createAsyncThunk('businessCards/updateBusinessCard', async ({ id, data }) => {
    return await updateBusinessCardApi(id, data);
});
// : 
export const deleteBusinessCard = createAsyncThunk('businessCards/deleteBusinessCard', async (id) => {
    await deleteBusinessCardApi(id);
    return id;
});
// : 
function filterCards(cards, searchQuery) {
    // cards
    if (!cards || !Array.isArray(cards)) {
        return [];
    }
    return cards.filter(card => {
        // 
        return searchQuery === '' ||
            card.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
            card.company_name.toLowerCase().includes(searchQuery.toLowerCase());
    });
}
// Business Cards Slice 
const businessCardsSlice = createSlice({
    name: 'businessCards',
    initialState,
    reducers: {
        // 
        setSearchQuery: (state, action) => {
            state.searchQuery = action.payload;
            state.filteredCards = filterCards(state.cards, state.searchQuery);
        },
        // 
        clearError: (state) => {
            state.error = null;
        },
        // 
        clearSuccessMessage: (state) => {
            state.successMessage = null;
        },
    },
    extraReducers: (builder) => {
        builder
            // 
            .addCase(fetchBusinessCards.pending, (state) => {
            state.loading = true;
            state.error = null;
        })
            // 
            .addCase(fetchBusinessCards.fulfilled, (state, action) => {
            state.loading = false;
            state.cards = action.payload;
            state.filteredCards = filterCards(state.cards, state.searchQuery);
        })
            // 
            .addCase(fetchBusinessCards.rejected, (state, action) => {
            state.loading = false;
            state.error = action.error.message || 'Failed to fetch business cards';
        })
            // 
            .addCase(createBusinessCardThunk.fulfilled, (state, action) => {
            state.cards.push(action.payload);
            state.filteredCards = filterCards(state.cards, state.searchQuery);
        })
            // 
            .addCase(updateBusinessCardThunk.fulfilled, (state, action) => {
            const index = state.cards.findIndex((card) => card.id === action.payload.id);
            if (index !== -1) {
                state.cards[index] = action.payload;
                state.filteredCards = filterCards(state.cards, state.searchQuery);
            }
        })
            // 
            .addCase(deleteBusinessCard.fulfilled, (state, action) => {
            state.cards = state.cards.filter((card) => card.id !== action.payload);
            state.filteredCards = state.filteredCards.filter((card) => card.id !== action.payload);
            state.successMessage = '';
            state.error = null;
        });
    },
});
// 
export const { setSearchQuery, clearError, clearSuccessMessage } = businessCardsSlice.actions;
export default businessCardsSlice.reducer;
