import { createSlice } from '@reduxjs/toolkit';
const initialState = {
    sidebarOpen: false, // : 
    modalOpen: false, // : 
    theme: 'light', // : 
    loading: false,
    toasts: [],
    error: null,
    deleteConfirm: {
        isOpen: false,
        cardId: null,
        cardName: ''
    }
};
const uiSlice = createSlice({
    name: 'ui',
    initialState,
    reducers: {
        // 
        toggleSidebar: (state) => {
            state.sidebarOpen = !state.sidebarOpen;
        },
        // 
        openSidebar: (state) => {
            state.sidebarOpen = true;
        },
        // 
        closeSidebar: (state) => {
            state.sidebarOpen = false;
        },
        // 
        toggleModal: (state) => {
            state.modalOpen = !state.modalOpen;
        },
        // 
        openModal: (state) => {
            state.modalOpen = true;
        },
        // 
        closeModal: (state) => {
            state.modalOpen = false;
        },
        // 
        setTheme: (state, action) => {
            state.theme = action.payload;
        },
        // 
        setLoading: (state, action) => {
            state.loading = action.payload;
        },
        // 
        setError: (state, action) => {
            state.error = action.payload;
        },
        // 
        clearError: (state) => {
            state.error = null;
        },
        // 
        openDeleteConfirm: (state, action) => {
            state.deleteConfirm = {
                isOpen: true,
                cardId: action.payload.cardId,
                cardName: action.payload.cardName
            };
        },
        // 
        closeDeleteConfirm: (state) => {
            state.deleteConfirm = {
                isOpen: false,
                cardId: null,
                cardName: ''
            };
        },
        addToast: (state, action) => {
            const toast = {
                ...action.payload,
                id: Date.now().toString(),
            };
            state.toasts.push(toast);
        },
        removeToast: (state, action) => {
            state.toasts = state.toasts.filter((toast) => toast.id !== action.payload);
        },
    },
});
export const { toggleSidebar, openSidebar, closeSidebar, toggleModal, openModal, closeModal, setTheme, setLoading, setError, clearError, openDeleteConfirm, closeDeleteConfirm, addToast, removeToast, } = uiSlice.actions;
export default uiSlice.reducer;
